import angular from 'angular'

const SUBSCRIPTION_CODE_QUERY_PARAM = 'subscription_code'

export default angular
  .module('SubscriptionReactControllerModule', [])
  .controller('SubscriptionReactController', function ($scope, $state, $stateParams, analytics) {
    $scope.initialSubscriptionCode = $stateParams[SUBSCRIPTION_CODE_QUERY_PARAM] || ''
    $scope.onAddSubscriptionPlanClick = () => {
      // Transition state to the subscription marketing page
      $state.go('main.subscriptions.marketing')
    }
    $scope.onRedeemSubscriptionSuccess = () => {
      // TODO: remove this event hook once analytics is ported over to react
      analytics.logSegmentEvent('subscription.redeem_gift_code', {
        success: true,
        source: 'account',
      })
    }
  })
